<template>
	<view>
		<view class="login-img">
			<view class="owl-login" :class="hideEyes?'password':''">
				<view class="hand"></view>
				<view class="hand hand-r"></view>
				<view class="arms">
					<view class="arm"></view>
					<view class="arm arm-r"></view>
				</view>
			</view>
		</view>
		<view class="login-form">
			<view class="title">邮箱登录</view>
			<form @submit="formSubmit">
				
				<view class="form-input">
					<input  @input="email" type="text"  value="" placeholder="邮箱" />					
				</view>
				<view class="form-input-code">
					<input type="text" value="" placeholder="验证码" @focus="passwordF_B"
							@blur="passwordF_B"  @input='ver_code'/>
					<button @click="get_code" :style="{color:get_code_text=='获取验证码'?'#000':'#47cc47'}">{{get_code_text}}</button>
				</view>
				<button type="primary" form-type="submit" :loading="loging?true : false">登录</button>
			</form>
		</view> 
		<uni-popup ref="login_fail_popup" type="center">
			<uni-popup-message type="error" message="邮箱或验证码错误，请重新输入！" :duration="3000"></uni-popup-message>
		</uni-popup> 
	</view>
</template>

<script>
	import login from "../../js/login.js";
	import uniPopup from '@/components/uni-popup/uni-popup.vue';
	import uniPopupMessage from '@/components/uni-popup/uni-popup-message.vue';
	import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue';
	var email='';
	var waiting=0;
	var count=0;
	var loop_id=0;
	var ver_code=';'
	export default {
		components: {
		        uniPopup,
		        uniPopupMessage,
		        uniPopupDialog 
		},
		data() {
			return {
				hideEyes: false,
				get_code_text:'获取验证码',
				loging:false
			}
		},
		methods: {
			passwordF_B() {
				this.hideEyes = !this.hideEyes;
			},
			formSubmit(e) {
				if(email!='' && ver_code!='')
				{
					this.loging=true;
					login.get_user_info_by_email(email,ver_code,this.get_user_info_cb);
				}
			},
			get_user_info_cb(res){
				// console.log(res);
				this.loging=false;
				if(res.data.errCode!=0){
					this.$refs.login_fail_popup.open();
				}
			},
			get_code(){
				// if()
				// console.log(this.$refs.email.value) 				
				if(waiting==0)
				{
					waiting=1;
					count=30;
					this.get_code_text=count-- +'S';
					login.send_ver_email(email);
					loop_id=setInterval(this.timer, 1000,'');
				}
				
			},
			timer(){	
				this.get_code_text=count+'S';
				if(count>0)
				{
					count--;
				}
				else
				{
					waiting=0;
					clearInterval(loop_id);
					this.get_code_text='获取验证码';
				}
			
				
			},
			email(e){
				// console.log(e.detail.value)
				email=e.detail.value;
			},
			ver_code(e){
				ver_code=e.detail.value;
			},
			
		}
	}
</script>

<style>
	.login-form {
		margin: -9px 0 0 0;
