		font-size: 4.44vw;
	}
	.login-form form{
		display: flex;
		flex-direction: column;
		align-items: center;
		/* background-color: #47CC47; */
		justify-content: center;
	}

	.login-form .title {
		display: flex;
		justify-content: center;
		font-size: 5vw;
